@import url(https://fonts.googleapis.com/css2?family=Antic+Slab&display=swap);
.container{margin-top:10vh;width:100%;text-align:center}.container .search-form{margin-top:3%;display:flex;align-items:center;justify-content:center;font-family:"Lato", sans-serif}.container .search-form .search-input{background:0;width:150px;outline:0;border:0;border-bottom:2px solid black;margin:20px 0;padding-bottom:10px;font-size:18px;font-weight:bold}.container .search-form .submit-btn{font-size:30px}

.modal-card{display:flex;justify-content:center}.modal-card .modal-header{display:flex;align-items:center}.modal-card .modal-header .modal-message{display:block;font-size:20px}.modal-card .close-btn{display:block;cursor:pointer;margin-left:5px;color:red;font-size:20px}

.section{margin-top:2%}.section .container{margin:auto;border-radius:15px;max-width:40%;min-width:336px;background-color:rgba(199,183,183,0.3)}.section .container .title{padding-top:3%}.section .container .results{margin-top:3%;height:15vh;display:flex;justify-content:space-around}.section .container .results .results-title{margin-bottom:15px}

*{margin:0;padding:0;font-family:"Antic Slab", serif}* body{height:100vh;overflow:hidden;background:linear-gradient(rgba(135,60,255,0.4), rgba(135,60,255,0) 80%),linear-gradient(-45deg, rgba(120,155,255,0.9) 25%, rgba(255,160,65,0.9) 75%);background-repeat:no-repeat;background-size:cover}* body h2{text-align:center}

